<template>
    <v-container fluid grid-list-xs>
        <loading-indicator v-bind:loading="loading"></loading-indicator>

        <v-toolbar extended
                  fixed 
                  scroll-off-screen
                  scroll-toolbar-off-screen
                  v-if="corps.name"
                  style="top: 56px;">

                <v-toolbar-title class="body-2 grey--text">
                  {{ corps.name }} 
                  (<span v-for="place in corps.places" v-bind:key="place._id"><span v-if="place._id !== ''">{{ place.code }} </span></span>)
                </v-toolbar-title>
            <v-container fluid grid-list-md slot="extension">  
              <v-layout row wrap>
                <v-flex xs4 v-if="corps.places">
                  <v-select
                    :items="corps.places"
                    v-model="selectedPlace"
                    label="Select"
                    single-line
                    combobox
                    item-text="code"
                    item-value="_id"
                  ></v-select>
                </v-flex>
                <v-flex xs4>
                  <v-select
                    :items="selectedPlace.audience"
                    v-model="selectedAudience"
                    label="Select"
                    single-line
                    combobox
                    item-text="name"
                    item-value="_id"
                  ></v-select>
                </v-flex>
                <v-flex xs4 text-xs-right>
                  <p class="headline">
                  
                  </p>
                </v-flex>
              </v-layout>
            </v-container>
        </v-toolbar>
        
        
        <v-layout column align-center style="margin-top: 106px;">
          <v-flex xs12>
              <v-data-table
                  :headers="headers"
                  :items="items"
                  item-key="_id"
                  :search="selectedAudience._id"
                  hide-actions
                  :loading="loading"
                  no-data-text="Ничего не найдено"
              >
                  <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
                  <template slot="items" slot-scope="props">
                    <pupil-table-row 
                      v-bind:props="props"
                      v-on:toggleExpand="toggleExpand"
                    ></pupil-table-row>
                  </template>
                  <template slot="expand" slot-scope="props">
                    <pupil-table-expand 
                      v-bind:pupil="props.item"
                      v-on:toggleEdit="editItemDialog"
                    ></pupil-table-expand>
                  </template>
              </v-data-table>

              <v-dialog v-model="dialog" max-width="500px">
                <v-card>
                  <v-card-title>
                    <span class="headline"> Статус </span>
                  </v-card-title>
                  <v-card-text>
                    <v-container grid-list-md>
                      <v-layout wrap>
                        <v-flex xs12 sm6 md4>
                          {{editedItem.firstName}}
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" flat @click.native="closeDialog">Cancel</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
          </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
  import apiService from '@/services/apiService'
  import CONSTANTS from '@/constants/constants'

  export default {
    data () {
      return {
        loading: true,
        dialog: false,
        editedIndex: -1,
        editedItem: {},
        defaultItem: {},
        corps: {},
        selectedPlace: {},
        selectedAudience: {},
        headers: CONSTANTS.TABLE_HEADERS,
        items: []
      }
    },

    created () {
      this.fetchCorps()
    },

    watch: {
      selectedPlace: function (value) {
        this.$router.push({name: 'Table', query: Object.assign({}, this.$route.query, { place: value._id })})
        this.placeChanged()
      },
      dialog (val) {
        val || this.closeDialog()
      }
    },

    methods: {
      toggleExpand (props) {
        props.expanded = !props.expanded
      },

      placeChanged () {
        this.selectedAudience = CONSTANTS.NONE_AUDIENCE
        this.fetch(this.$route.query)
      },

      editItemDialog (item) {
        this.editedIndex = this.items.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      closeDialog () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      fetch (params) {
        this.loading = true

        apiService.getPupils(params)
          .then(this.onPupilsSuccess)
          .catch(this.onError)
          .finally(this.loadingEnd)
      },

      fetchCorps () {
        apiService.getCorps(this.$route.query.corps)
          .then(this.onCorpsSuccess)
          .catch(this.onError)
      },

      onPupilsSuccess (response) {
        this.items = response.data
      },

      onCorpsSuccess (response) {
        const routPlace = this.$route.query.place || ''
        let i = 0
        let length = response.data.places.length
        let selectedPlaceIndex = 0
        let allAudience = []

        this.corps = response.data

        if (length > 1) {
          this.corps.places.unshift(CONSTANTS.NONE_PLACE)
          length = length + 1

          for (i; i < length; i++) {
            if (this.corps.places[i]._id === routPlace) {
              selectedPlaceIndex = i
            }
            allAudience = allAudience.concat(this.corps.places[i].audience)
          }
          this.corps.places[0].audience = allAudience.sort(sortAudienceByCode)
        }
        this.selectedPlace = this.corps.places[selectedPlaceIndex]
      },

      getFIO (item) {
        return `${item.firstName} ${item.lastName} ${item.parentName}`
      },

      loadingEnd () {
        this.loading = false
      },

      onError (error) {
        console.log(error)
      }
    }
  }

  function sortAudienceByCode (a, b) {
    return a.code >= b.code
  }
</script>


